.advantages {
  background: #f8fdff;
  color: $color-default-black;
  padding-top: 120px;
  padding-bottom: 125px;

  @include vp-1219 {
    padding: 80px 0;
  }

  @include vp-769 {
   padding: 50px 0;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;

  @include vp-769 {
    grid-template-columns: 1fr;
  }
}

.advantages__item {
  padding: 40px 45px 35px 45px;
  border: 1px solid #ebebeb;
  border-left: 5px solid #00b2ff;
  min-height: 300px;

  &:hover {
    background-color: #00b2ff;
    cursor: default;
  }

  &:hover .advantages__svg {
    fill: $color-light;
  }

  &:hover h3,
  &:hover p {
    color: $color-light;
  }

  @mixin hover-focus {
    @media (hover: hover) {
      &:hover:not(.focus-visible) {
        @content;
      }
    }

    &.focus-visible:focus {
      @content;
    }
  }

  @include vp-1219 {
    min-height: 360px;
  }

  @include vp-769 {
    padding: 30px 30px 35px 25px;
    min-height: 340px;
  }
}

.advantages__svg {
  fill: #00b2ff;

  @include vp-769 {
    width: 30px;
    height: 30px;
  }
}

.advantages__item h3 {
  font-size: 24px;
  line-height: 34px;
  color: #00b2ff;
  margin-top: 20px;
  margin-bottom: 15px;

  @include vp-769 {
    font-size: 20px;
    line-height: 28px;
  }
}

.advantages__item p {
  @include vp-769 {
    font-size: 14px;
    line-height: 19px;
  }
}
